.jonnitto-prettyembed {
  position: relative;
  display: block;
  width: 100%;
  max-width: 100%;
}

.jonnitto-prettyembed img {
  display: block;
  width: 100%;
  height: auto;
}

.jonnitto-prettyembed--video::after, .jonnitto-prettyembed--iframe:not(.jonnitto-prettyembed--play):not(.jonnitto-prettyembed__content)::after {
  display: block;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  size: size("PlayButton.png", 2);
  opacity: 0.8;
  transform: translate(-50%, -50%) scale(0.8);
  background: inline("PlayButton.png") center no-repeat;
  background-size: contain;
  transition: transform 0.25s ease-in-out, opacity 0.25s linear;
  pointer-events: none;
  z-index: 1;
}

.jonnitto-prettyembed--video:hover::after, .jonnitto-prettyembed--iframe:not(.jonnitto-prettyembed--play):not(.jonnitto-prettyembed__content):hover::after {
  transform: translate(-50%, -50%) scale(1);
}

.jonnitto-prettyembed--video.jonnitto-prettyembed--play.jonnitto-prettyembed--slim::after {
  opacity: 0;
  background: inline("PauseButton.png") center no-repeat;
  background-size: size("PlayButton.png", 4);
  top: height("PlayButton.png", 4);
  left: width("PlayButton.png", 4);
}

.jonnitto-prettyembed--video.jonnitto-prettyembed--play.jonnitto-prettyembed--slim:hover::after {
  opacity: 1;
}

.jonnitto-prettyembed--video.jonnitto-prettyembed--play:not(.jonnitto-prettyembed--slim)::after {
  display: none;
}

.jonnitto-prettyembed--lightbox {
  cursor: pointer;
}

.jonnitto-prettyembed__lightbox {
  position: fixed 0;
  z-index: -1;
  opacity: 0;
  font-size: 0;
  transition: opacity 0.2s, z-index 0s 0.2s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  overflow-y: auto;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  pointer-events: none;
  background: rgba(11, 11, 11, 0.8);
  padding: 15px;
}

.-jonnitto-prettyembed__lightbox .jonnitto-prettyembed__lightbox {
  z-index: 1500;
  opacity: 1;
  transition: opacity 0.2s;
  pointer-events: initial;
}

.jonnitto-prettyembed__inner {
  width: 100%;
  max-width: 900px;
  transform: scale(0);
  transition: transform 0.2s;
}

.-jonnitto-prettyembed__lightbox .jonnitto-prettyembed__inner {
  transform: scale(1);
}

.jonnitto-prettyembed__content {
  line-height: 0;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
}

.jonnitto-prettyembed__close {
  position: relative;
  display: block;
  z-index: 1;
  padding: 10px 16px;
  margin: calc(-1em - 10px) -18px -8px auto;
  font-size: 30px;
  font-family: Arial, Baskerville, monospace;
  font-style: normal;
  line-height: 1;
  cursor: pointer;
  user-select: none;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
  outline: none;
  box-shadow: none;
  text-decoration: none;
  color: #fff;
  opacity: 0.65;
  transition: opacity 0.2s;
}

.jonnitto-prettyembed__close:hover, .jonnitto-prettyembed__close:focus, .jonnitto-prettyembed__close:active {
  opacity: 1;
  outline: none;
}

.jonnitto-prettyembed__close::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.jonnitto-prettyembed video {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  max-width: 100%;
  outline: none;
  opacity: 0;
}

.jonnitto-prettyembed video:not([controls]):not([autoplay]) {
  cursor: pointer;
}

.jonnitto-prettyembed--video img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: auto;
  transform: translate(-50%, -50%);
  display: block;
  transition: opacity 0.3s linear;
  pointer-events: none;
}

.jonnitto-prettyembed--video.jonnitto-prettyembed--autoplay {
  pointer-events: none;
}

.jonnitto-prettyembed--video.jonnitto-prettyembed--autoplay::after {
  display: none;
}

.jonnitto-prettyembed--video.jonnitto-prettyembed--autoplay video, .jonnitto-prettyembed--video.jonnitto-prettyembed--init video {
  opacity: 1;
}

.jonnitto-prettyembed--video.jonnitto-prettyembed--autoplay img, .jonnitto-prettyembed--video.jonnitto-prettyembed--init img {
  opacity: 0;
}

.jonnitto-prettyembed__lightbox video {
  width: auto;
  max-height: calc(99vh - 52.5px);
}

.jonnitto-prettyembed--iframe iframe {
  border: 0 solid transparent;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
